import axios from 'axios'; //    http   fetch

//     
export default class ServicePictures {
  constructor() {
    this.URL = 'https://pixabay.com/api/';
    this.API_KEY = '36560176-389977c64a520682aab26cdb9';
    this.perPage = 40;
    this.page = 1;
    this.searchQuery = '';
    this.totalHits = 0; //   . totalHits    pixabay (   )
  }
  // axios     js     .json     fetch
  async getPictures() {
    const params = {
      key: this.API_KEY,
      q: this.searchQuery,
      image_type: 'photo',
      orientation: 'horizontal',
      safesearch: true,
      per_page: this.perPage,
      page: this.page,
    };
    const { data } = await axios.get(`${this.URL}`, { params }); //   params   , data     ,  
    //  GET-      .  { data } ,       data  - .  data   ,     GET-.
    //  ,     ,  data   ,     GET-.

    this.incrementPage();
    this.totalHits = data.totalHits; //    ,    
    return data.hits; //hits    (     post)    ,   data
  }

  resetPage() {
    this.page = 1;
  }

  incrementPage() {
    this.page += 1;
  }
}
