import './css/styles.css';

import Notiflix from 'notiflix';
import debounce from 'lodash.debounce';

import refs from './references';

import ServicePictures from './getPictures';
import { createMarkup, updateList, clearList, onError } from './functions';
import LoadMoreBtn from './LoadMoreBtn';

//       LoadMoreBtn.      .    
const loadMoreBtn = new LoadMoreBtn({
  selector: '.load-more',
  isHidden: true,
});
const servicePictures = new ServicePictures(); //        pi

refs.form.addEventListener('submit', onSearchPictures);
loadMoreBtn.button.addEventListener('click', onLoadMore);

async function onSearchPictures(event) {
  loadMoreBtn.hide();
  event.preventDefault();
  const form = event.currentTarget;
  const value = form.elements.searchQuery.value.trim(); // searchQuery    ,    

  if (value === '') {
    Notiflix.Notify.failure('Oops, no value...');
    clearList();
  } else {
    try {
      servicePictures.searchQuery = value; //        searchQuery   
      servicePictures.resetPage(); //    1

      clearList(); //    ,   

      const data = await servicePictures.getPictures(); //    
      const markup = data.reduce(
        (markup, picture) => markup + createMarkup(picture),
        ''
      ); // 
      loadMoreBtn.show(); //    
      if (!data) {
        loadMoreBtn.hide();
        return ''; //     (undefined).           
      } else if (data.length === 0) {
        Notiflix.Notify.failure(
          'Sorry, there are no images matching your search query. Please try again.'
        ); //    
        servicePictures.resetPage();
        loadMoreBtn.hide();
        form.reset(); //      
        return;
      } else if (data.length < servicePictures.perPage) {
        Notiflix.Notify.success(
          `Hooray! We found ${servicePictures.totalHits} images.`
        ); //       1 ,  ,  
        updateList(markup);
        loadMoreBtn.hide();
        form.reset();
      } else {
        Notiflix.Notify.success(
          `Hooray! We found ${servicePictures.totalHits} images.`
        );
        updateList(markup);
        loadMoreBtn.show();
        form.reset();
      }
    } catch (err) {
      onError(err);
    }
  }
}

//    
async function onLoadMore() {
  loadMoreBtn.disable(); //   " "

  try {
    const data = await servicePictures.getPictures();
    const markup = data.reduce(
      (markup, picture) => markup + createMarkup(picture),
      ''
    );
    updateList(markup);
    loadMoreBtn.enable(); //  
    if (!data) {
      loadMoreBtn.hide();
      return ''; //     (undefined).           
    }
    //  ,    2- 
    if (servicePictures.page > 1) {
      const { height: cardHeight } = document
        .querySelector('.gallery')
        .firstElementChild.getBoundingClientRect();

      window.scrollBy({
        top: cardHeight * 2,
        behavior: 'smooth',
      });
    }
    //     totalHits
    if (
      servicePictures.page * servicePictures.perPage >=
      servicePictures.totalHits
    ) {
      Notiflix.Notify.info(
        "We're sorry, but you've reached the end of search results."
      );
      loadMoreBtn.hide();
    }
  } catch (err) {
    onError(err);
  }
}

